import { useRouter } from 'vue-router'
import panelControlVideo from '@/assets/videos/panel-control-scenario.mp4'
import poseVideo from '@/assets/videos/pose-scenario.mp4'

export default {
  name: "Home",
  components: {
    Carousel, Slide, Navigation
  },
  setup() {
    const router = useRouter();

    // Obtener las rutas del router
    const routerRoutes = router.getRoutes().filter(route => route.path.includes('scenario'))

